import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
// 懒加载路由
const home = resolve => require(['@/pages/home/index'], resolve)
const common = resolve => require(['@/pages/common/nav'], resolve)
const message = resolve => require(['@/pages/message/index'], resolve)
const my = resolve => require(['@/pages/my/index'], resolve)
const myRecord = resolve => require(['@/pages/my/myRecord'], resolve)
const myRecordList = resolve => require(['@/pages/my/myRecordList'], resolve)
const myRecordDetail = resolve => require(['@/pages/my/myRecordDetail'], resolve)

const loveCornerDetail = resolve => require(['@/pages/home/loveCornerDetail'], resolve)
const resumeDetail = resolve => require(['@/pages/home/resumeDetail'], resolve)
const resume = resolve => require(['@/pages/resume/resume'], resolve)

// 消息
const messageDetail = resolve => require(['@/pages/message/msgDetail'], resolve)
const myResume = resolve => require(['@/pages/my/myResume'], resolve)
const editResume = resolve => require(['@/pages/my/editResume'], resolve)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/common',
      name: 'common',
      component: common,
      redirect: '/home',
      children: [
        {
          path: '/home',
          name: 'home',
          component: home,
          meta: {
            title: '儿女相亲'
          }
        }, {
          path: '/message',
          name: 'message',
          component: message,
          meta: {
            title: '消息'
          }
        }, {
          path: '/my',
          name: 'my',
          component: my,
          meta: {
            title: '我的'
          }
        }
      ]
    },
    {
      path: '/cornerDetail',
      name: 'cornerDetail',
      component: loveCornerDetail,
      meta: {
        title: '相亲角详情'
      }
    },
    {
      path: '/resumeDetail',
      name: 'resumeDetail',
      component: resumeDetail,
      meta: {
        title: '简历详情'
      }
    },
    {
      path: '/messageDetail',
      name: 'messageDetail',
      component: messageDetail,
      meta: {
        title: '消息详情'
      }
    },
    {
      path: '/myRecord',
      name: 'myRecord',
      component: myRecord,
      meta: {
        title: '我的记录'
      }
    },
    {
      path: '/myRecordList',
      name: 'myRecordList',
      component: myRecordList,
      meta: {
        title: '我的记录'
      }
    },
    {
      path: '/myRecordDetail',
      name: 'myRecordDetail',
      component: myRecordDetail,
      meta: {
        title: '我的记录'
      }
    }, {
      path: '/resume',
      name: 'resume',
      component: resume,
      meta: {
        title: '简历'
      }
    }, {
      path: '/myResume',
      name: 'myResume',
      component: myResume,
      meta: {
        title: '简历'
      }
    }, {
      path: '/editResume',
      name: 'editResume',
      component: editResume,
      meta: {
        title: '简历'
      }
    },
    {
      path: '*',
      redirect: home
    }

  ]
})



